white .container .navbar-brand{color:#000}header .navbar-custom.white .container ul li a{color:#000}header .navbar-custom.white .container ul li .products-drop{color:#000}header .navbar-custom.white .container ul li .products-drop .product-dropdown .dropdown-content{background-color:#fff}header .navbar-custom.white .container ul li .products-drop .product-dropdown .dropdown-content a{color:black}header .navbar-custom.white .container ul li .products-drop .product-dropdown .dropdown-content a:hover{color:#431f85}@media screen and (max-width: 768px){header .navbar-custom{height:3.75rem}header .navbar-custom .container .navbar-brand{font-size:0.75rem}header .navbar-custom .container .navbar-brand img{width:25px;margin-right:0.635rem}header .navbar-custom .container ul li:not(:first-child){margin-left:1.25rem}header .navbar-custom .container ul li a{font-size:0.625rem}header .navbar-custom .container ul li .products-drop{font-size:0.625rem}}


